.blog-index-header-main{
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 650px;
  max-height: 100vh;
  background: $primary;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
.blog-index-title{
  position: relative;
  z-index: 5;
  text-align: center;
  padding-top: 50px;
  h1.featured-blog-post-title{
    max-width: 600px;
    font-size: 3.125em;
    font-weight: 400;
    line-height: 1.2em;
    padding: 0 20px 5px 20px;
    color: $white;
    margin: 30px 0;
    @media screen and(max-width: 500px){
      font-size: 2.5em;
    }
    @media screen and(max-width: 400px){
      font-size: 2.1875em;
    }
    @media screen and(max-width: 360px){
      font-size: 1.875em;
    }
  }
  a.read-featured-blog-post{
    color: $white;
    font-size: 0.75em;
    font-weight: 500;
    padding: .5em 2em;
    border: 2px solid $white;
    border-radius: 4px;
    text-transform: uppercase;
    &:hover{
      background-color: $white;
      color: $highlight;
    }
  }
}
.blogIndexWrapper{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: baseline;
  flex-wrap: wrap;
  width: 100%;
  max-width: 1200px;
  margin: 100px auto 0 auto;
  padding: 0 20px;
  @media screen and(max-width: 700px){
    padding: 0;
  }
}
.blogPostContainer{
  flex: 1 0 auto;
  transition: all 0.4s ease-in;
  width: 100%;
  max-width: 320px;
  margin: 0 5px 120px 5px;
  flex-basis: 320px;
}
@media screen and(max-width: 700px){
  .blogPostContainer{
    max-width: 100%;
    flex-basis: 100%;
    margin: 0 0px 120px 0px;
  }
}
.blogPostFeatImg{
  display: block;
  width: 100%;
  height: 200px;
  margin: 0 auto 40px auto;
  border-radius: 3px;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  @media screen and(max-width: 700px){
    border-radius: 0px;
  }
}
.blogCategory{
  display: block;
  text-align: center;
  margin: 0 auto 15px auto;
  color: $secondary;
  text-transform: uppercase;
  font-family: $font-stack;
  font-size: .5em;
  letter-spacing: 1px;
}
.blogTitle{
  display: block;
  text-align: center;
  margin: 0 auto 20px auto;
  font-family: $font-stack;
  font-size: 1.2em;
  font-weight: 500;
  color: $highlight;
}
.blogExcerpt{
  text-align: center;
  margin: auto;
  color: $secondary;
  font-family: $font-stack-two;
  font-size: .8em;
  line-height: 2em;
}
.blogCategory, .blogTitle, .blogExcerpt{
  padding: 0 20px;
}